import React ,{Component}from 'react';
//import logo from './logo.svg';
import MovieRow from './MovieRow.js';

class MoviePage extends Component {
  constructor(props){
    super(props)
    this.state={top_rated : true}
    this.performSearch("")
  }

 performSearch(searchTerm){
  let url;
  console.log(searchTerm)
  if (searchTerm == null || searchTerm=="") {
     this.setState({top_rated: true})
    url = 'https://api.themoviedb.org/3/movie/top_rated?api_key=6d33d7313e2e4565a9681b4714dd4dc7&language=en-US&page=1';
  }
  else{
    this.setState({top_rated: false})
    url = 'https://api.themoviedb.org/3/search/movie?&api_key=6d33d7313e2e4565a9681b4714dd4dc7&query='+searchTerm;
  }
  fetch(url)
  .then(response=> response.json())
  .then(rows => {
    var movieRows = []
    rows.results.map((movie)=> {
      movie.poster_src = "https://image.tmdb.org/t/p/w185" + movie.poster_path
      const movieRow = <MovieRow key={movie.id} movie={movie}/>
      movieRows.push(movieRow)
    })
    this.setState({rows: movieRows})
  });
  }

  searchChangeHandler(event){
    const boundObject = this;
    const searchTerm = event.target.value;
    boundObject.performSearch(searchTerm);
  }
  render(){
    if (this.state.top_rated == true) {
     var top = "Top Rated"
    }
    else{
      var top = ""
    }    
    return (
      <div className="App">
        <table className="titleBar">
          <tbody>
            <tr>
              <td>
                <img className="App-logo" src="logo192.png"/>  
              </td>
              <td width="8"></td>
              <td>
                <h2>The Movie Search</h2>
              </td>
            </tr>
          </tbody>
        </table>

        <input onChange={this.searchChangeHandler.bind(this)} className="inputText" placeholder="Enter Search term"/>
        
          <h2 id="top">{top}</h2>
        
        {this.state.rows}
      </div>
    );
  }
}

export default MoviePage;
