import React ,{Component}from 'react';
import MoviePage from './MoviePage.js';
import './LoginPage.css';
import {Button , Form , FormGroup, Label, Input} from 'reactstrap';
import {Link} from 'react-router-dom';

class RegisterPage extends Component {

	constructor(props){
		super(props);
		this.state = {
			email: '',
			password:'',
			name:''
		}
	}

	onEmailChange(event){
		this.setState({email :event.target.value})
	}

	onPasswordChange(event){
		this.setState({password :event.target.value})	
	}

	onNameChange(event){
		this.setState({name :event.target.value})
	}

	moveToSite(){
		fetch('https://search-movies-app.herokuapp.com/register',{
			method: 'post',
			headers: {'Content-Type':'application/json'},
			body:JSON.stringify({
				email: this.state.email,
				password: this.state.password,				
				name: this.state.name				
			})
		})
		.then(response => response.json())
		.then(user=> {
			if (user.id) {
				//this.props.loadUser(user)
				this.props.history.push('/MoviePage');	
			}
			else{
				this.props.history.push('/ErorPage');	
			}
		})
	}

    render(){
      return(
      	<Form className="Login-form">
	        <h1 className="text-center">
	        	<span className="font-weight-bold">The Search-Movies</span>
	        </h1>
	        <h2 className="text-center">Welcome </h2>
	        <FormGroup>
	        	<Label>Name</Label>
	        	<Input type="text" placeholder="Name" id="inputName" onChange={this.onNameChange.bind(this)}/>
	        </FormGroup>
	        <FormGroup>
	        	<Label>Email</Label>
	        	<Input type="email" placeholder="Email" id="inputEmail" onChange={this.onEmailChange.bind(this)}/>
	        </FormGroup>
	        <FormGroup>
	        	<Label>Password</Label>
	        	<Input type="password" placeholder="Password" id="inputPassword" onChange={this.onPasswordChange.bind(this)}/>
	        </FormGroup>
	       	<Button className="btn-lg btn-dark btn-block" onClick={this.moveToSite.bind(this)}>
	       	Register
	       	</Button>	       
	       	<div className="text-center">
	       		<Link to='/'>
	       			<a>Sign in with your account</a>
	       		</Link>   				
	       	</div>	   
        </Form>
      );
  }
}

export default RegisterPage;
