import React ,{Component}from 'react';
//import $ from 'jquery';
class MovieRow extends Component {
 constructor(props){
    super(props)
    this.state={moreText : true}    
  }
  viewMovie(){
    const url = "https://www.themoviedb.org/movie/"+this.props.movie.id
    window.location.href = url;
  }
    
    
  showMore(){
    var p = document.getElementById("p"+this.props.movie.id);
    var li = document.getElementById("link"+this.props.movie.id);      
    if (this.state.moreText === true) {      
      console.log("showMore+") 
      this.state.moreText = false      
      p.innerHTML = this.props.movie.overview              
    }else{            
      p.innerHTML = this.props.movie.overview.substring(0,200)+"...more"    
      console.log("showMore-") 
      this.state.moreText = true      
    }            
  }

  shrinkOverview(){
    var p = document.getElementById(this.props.movie.id);
    p.innerHTML = this.props.movie.overview.substring(0,200)    
    console.log("shrinkOverview");  
  }

  componentDidMount(){   
    var overview = this.props.movie.overview.substring(0,200)
    var myLi = "link"+this.props.movie.id;
    var li = document.getElementById(myLi);
    var p = document.getElementById(this.props.movie.id);
    console.log(p) 
   if (this.props.movie.overview.length > 200) {
      console.log("1")      
      li.innerHTML = "...more"      
      console.log(li.innerHTML)
    }else{
      console.log("2")
       li.innerHTML = ""
    } 
  }

  render(){    
    return <table key={this.props.movie.id}>
      <tbody>
        <tr>
          <td>
            <img alt="poster" width="120" src={this.props.movie.poster_src}/>
          </td>

          <td>
            <h3>{this.props.movie.title}</h3>          
            <p id={"p"+this.props.movie.id} onClick={this.showMore.bind(this)}>
              {this.props.movie.overview.substring(0,200)}
              <il id={"link"+this.props.movie.id} onClick={this.showMore.bind(this)} href="#">a</il>                 
            </p>        
            <input type="button" onClick={this.viewMovie.bind(this)} value="View"/>            
          </td>
        </tr>
      </tbody>
    </table>    
  }
}
export default MovieRow;