import React ,{Component}from 'react';
import MoviePage from './MoviePage.js';
import './LoginPage.css';
import {Button , Form , FormGroup, Label, Input} from 'reactstrap';
import {Link} from 'react-router-dom';

class LoginPage extends Component {
	constructor(props){
		super(props);
		this.state = {
			signInEmail: '',
			signInPassword:''
		}
	}

	onEmailChange(event){
		this.setState({signInEmail :event.target.value})
	}

	onPasswordChange(event){
		this.setState({signInPassword :event.target.value})	
	}

	moveToSite(){
		fetch('https://search-movies-app.herokuapp.com/signin',{
			method: 'post',
			headers: {'Content-Type':'application/json'},
			body:JSON.stringify({
				email: this.state.signInEmail,
				password: this.state.signInPassword				
			})
		})
		.then(response => response.json())
		.then(user=> {
			if (user.id) {
				//this.props.loadUser(user)
				this.props.history.push('/MoviePage');	
			}
			else{
				this.props.history.push('/ErorPage');	
			}
		})
	}
		/*
		let inputEmail = document.getElementById("inputEmail");
		let inputPassword = document.getElementById("inputPassword");
		console.log(inputEmail.value);
		if (inputEmail.value === "renan@gmail.com" && inputPassword.value === '1234') {
			this.props.history.push('/MoviePage');
		}else{
			this.props.history.push('/ErorPage');
		}*/
	


    render(){
      return(
      	<Form className="Login-form">
	        <h1 className="text-center">
	        	<span className="font-weight-bold">The Search-Movies</span>
	        </h1>
	        <h2 className="text-center">Welcome </h2>
	        <FormGroup>
	        	<Label>Email</Label>
	        	<Input type="email" placeholder="Email" id="inputEmail" onChange={this.onEmailChange.bind(this)}/>
	        </FormGroup>
	        <FormGroup>
	        	<Label>Password</Label>
	        	<Input type="password" placeholder="Password" id="inputPassword"onChange={this.onPasswordChange.bind(this)}/>
	        </FormGroup>
	       	<Button className="btn-lg btn-dark btn-block" onClick={this.moveToSite.bind(this)}>
	       	Log in
	       	</Button>
	       	<div className="text-center">
	       		<Link to='/RegisterPage'>
	       			<a>Sign up</a>
	       		</Link>
   				<span className="p-2">|</span>
	       		<Link to='/Forgot-password'>
		       		<a>Forgot Password</a>
	       		</Link>	       		
	       	</div>	       		   
        </Form>
      );
  }
}

export default LoginPage;
